export const setGroupAccess = (groupName, accessType) => async (dispatch, getState) => {
    dispatch(setLoadingAC());
    const username = getState().authReducer.user.username;
    const group = await GroupService.getGroup(username, groupName);
    group.accessType = accessType;
    const response = await GroupService.updateGroup(username, groupName, group);
    
    if(response.status == 200) {
        dispatch(setCurrentGroupAC(group));
        setErrorsAC([]);
    } else {
        setErrorsAC(['There has been an error.']);
    }
}

export const deleteGroup = (groupName) => async (dispatch, getState) => {
    dispatch(setLoadingAC());
    const username = getState().authReducer.user.username;
    const response = await GroupService.deleteGroup(username, groupName);

    if(response.status == 200) {
        dispatch(deleteGroupAC(groupName));
        dispatch(setErrorsAC([]));
        // todo: set success msg
    } else {
        dispatch(setErrorsAC(['There has been an error.']));
    }
}


export const loadGroups = () => async (dispatch, getState) => {
    dispatch(setLoadingAC());
    const username = getState().authReducer.user.username;
    const groups = await GroupService.getUserGroups(username);
    console.log('getUserGroups', groups);
    if(groups?.length) {
        dispatch(setGroupsAC(groups));
    } else {
        dispatch(setGroupsAC([]))
    }
}

export const addUserToGroup = (groupName, usernameToAdd) => async (dispatch, getState) => {
    dispatch(setLoadingAC());
    const username = getState().authReducer.user.username;
    const userToAdd = await UserService.getUser(usernameToAdd);
    
    if(userToAdd?.username) {
        const response = await GroupService.addUsersToGroup(username, groupName, [userToAdd]);

        if(response.status = 200) {
            dispatch(addUserAC(userToAdd));
            dispatch(setErrorsAC([]));
            // todo: set success msg
        } else {
            dispatch(setErrorsAC(['There has been an error.']));
        }
    }
}

export const removeUserFromGroup = (groupName, usernameToRemove) => async (dispatch, getState) => {
    dispatch(setLoadingAC());
    const username = getState().authReducer.user.username;
    const userToRemove = await UserService.getUser(usernameToRemove);
    
    if(userToRemove?.username) {
        const response = await GroupService.removeUsersFromGroup(username, groupName, [userToRemove]);

        if(response.status = 200) {
            dispatch(removeUserAC(usernameToRemove));
            dispatch(setErrorsAC([]));
            // todo: set success msg
        } else {
            dispatch(setErrorsAC(['There has been an error.']));
        }
    }
}